import React, { useState, createContext, useEffect } from 'react';
import { Container } from '@material-ui/core';
import './App.scss';
import Header from './components/Header'
import Main from './components/Main'
import Footer from './components/Footer'
import DesktopWarning from './components/DesktopWarning'
import firebase from "./firebase";
import 'firebase/firestore';
import { Mobile, Desktop } from "./components/deviceDetect";

export const MenuContext = createContext()

function App() {

  const [displayed, setDisplayed] = useState("Tickets")
  const [tickets, setTickets] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      const db = firebase.firestore();
      const data = await db.collection("tickets").get();
      setTickets(data.docs.map(doc => ({ ...doc.data(), id: doc.id })));
    };
    fetchData();
  }, []);

  return (
    <Container disableGutters={true}>
      <MenuContext.Provider value={{ displayed, setDisplayed, tickets }}>
        <Mobile>
          <Header></Header>
          <Main />
          <Footer></Footer>
        </Mobile>

        <Desktop>
          <DesktopWarning />
        </Desktop>
      </MenuContext.Provider>
    </Container>
  );
}

export default App;
