import React from 'react';
import { MenuContext } from '../App';
import {useContext} from 'react'
import { Menu, MenuItem} from '@material-ui/core';

const NavigationMenu = () => {

  const {setDisplayed} = useContext(MenuContext);
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClickListItem = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleClick = (btn) => {
    setDisplayed(btn)
    setAnchorEl(null);
  }

 return(
  <div className="NavMenu">
    <button className="NavMenuBtn" onClick={handleClickListItem}>
      <p className="hamburger">|||</p>
    </button>
    <Menu
      id="simple-menu"
      anchorEl={anchorEl}
      keepMounted
      open={Boolean(anchorEl)}
      onClose={handleClose}
    >
      <MenuItem onClick={() => handleClick("Skaner")}>Skaner</MenuItem>
      <MenuItem onClick={() => handleClick("Tickets")}>Bilety</MenuItem>
      <MenuItem onClick={() => handleClick("Promocje")}>Promocje</MenuItem>
      <MenuItem onClick={() => handleClick("Offline")}>Offline Mode</MenuItem>
      <MenuItem onClick={() => handleClick("Admin")}>Admin</MenuItem>
      <MenuItem onClick={() => handleClick("Logout")}>Logout</MenuItem>
    
    </Menu>
  </div>
 )
}

export default NavigationMenu;