import { useSignInWithEmailAndPassword } from 'react-firebase-hooks/auth';
import React, {useState} from 'react'
import firebase from 'firebase'

const SignIn = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [
    signInWithEmailAndPassword,
    user,
    loading,
    error,
  ] = useSignInWithEmailAndPassword(firebase.auth());


  return (
    <div className="SignIn">
      <div className="SignIn_section_one">
        <p>Welcome To QR Reader!</p>
        <p>Time 2 Party</p>
        {/* wyswietlanie wczytywania oraz wyswietlanie bledow jesli sa */}
        {loading? <p>Loading...</p> : error? <p>{error.message}</p> : null}
      </div>
      <div className="SignIn_section_two">
      <p className="SignIn_please_sign_in">Please Sign in</p>
      <input
        className="SignIn_login"
        type="email"
        placeholder="login"
        value={email}
        onChange={(e) => setEmail(e.target.value)}
     />
      <input
        className="SignIn_password"
        type="password"
        placeholder="password"
        value={password}
        onChange={(e) => setPassword(e.target.value)}
        />
      <button 
        className="SignIn_button" 
        onClick={() => signInWithEmailAndPassword(email, password)}>
        Let's Start!
      </button>
        </div>
    </div>
  );
};

export default SignIn